import React, { useContext, useState } from 'react';
import ButtonCus from '../../../components/ButtonCus';
import SectionTitle from '../../SectionTitle';
import SignInUp from '../../poupSignInUp/index'
import { AppContext } from '../../../context/AppProvider';

function HomeSignIn(props:any) {
      const {t} = useContext(AppContext)
      const [openSignIn, setOpenSignIn] = useState(false);
      const [activeTabSign, setActiveTabSign] = useState(1);
      const popupSignInClick=()=>{
            setOpenSignIn(true)
            setActiveTabSign(1)
      }
      const popupSignUpClick=()=>{
            setOpenSignIn(true);
            setActiveTabSign(2)
      }
      return (
            <div className="home-sign">
                  <SectionTitle
                        title={t('Home.Sign_tile')}
                        textAlign='center'
                  />
                  <div className="home-sign-button">
                        <ButtonCus
                              onClick={popupSignInClick}
                              text={t('Home.Sign_up')}
                              backColor="var(--purple)"
                              color='var(--bg-gray)'
                              fontSize="20px"
                              lineHeight='24px'
                              margin='36px 12px'
                              borderRadius='20px'
                        />
                        <ButtonCus
                              onClick={popupSignUpClick}
                              text={t('Home.Sign_in')}
                              color='var(--purple)'
                              fontSize="20px"
                              lineHeight='24px'
                              border='solid 1px var(--purple)'
                              margin='36px 12px'
                              borderRadius='20px'
                        />
                  </div>
                  <div className="home-sign">
                        <SignInUp
                              openSignIn={openSignIn}
                              setOpenSignIn={setOpenSignIn}
                              activeTabSign={activeTabSign}
                              setActiveTabSign={setActiveTabSign}
                        />
                  </div>
            </div>
      );
}

export default HomeSignIn;